}
.price__value-new {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
}

.request__container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 15px;
  margin: 0 30px;
}
.request__text {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 16px;
}
.request__title {
  font-size: 32px;
  font-weight: 900;
  line-height: 1.5;
}
.request__paragraph {
  color: #839aa9;
  color: var(--color-secondary-text);
  font-weight: 400;
  font-size: 22px;
}
.request__form {
  width: 100%;
}
.request__form-field {
  margin-bottom: 24px;
  width: 100%;
}

.reviews {
  margin: 25px 0;
}
.reviews__title {
  margin: 0 0 30px;
  font-weight: 900;
  font-size: 27px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
  color: #20222d;
}
.reviews__title::after {
  display: block;
  content: "";
  width: 62px;
  height: 5px;
  margin: 15px auto;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_8___}) center center no-repeat;
}
.reviews__slider {
  display: flex;
  gap: 8px;
  align-items: center;
}
.reviews__container {
  overflow: hidden;
}
.reviews__list {
  align-items: center;
  margin-bottom: 24px;
}

.size {
  margin: 25px 0;
}
.size__title {
  margin: 0 0 30px;
  font-weight: 900;
  font-size: 27px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
}
.size__title::after {
  display: block;
  content: "";
  width: 62px;
  height: 5px;
  margin: 15px auto;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_8___}) center center no-repeat;
}
.size__item {
  display: block;
  margin: 0 auto 20px;
