ion: ease-in;\n  }\n}\n.hide {\n  opacity: 0;\n  visibility: hidden;\n  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;\n}\n\n.show {\n  opacity: 1;\n  visibility: visible;\n}\n\n.accent {\n  color: var(--color-brand-yellow);\n}\n\n.border-right {\n  border-right: 2px solid #ebebeb;\n}\n\n.bolt {\n  font-weight: 800;\n}\n\n:root {\n  --color-primary: #eeeeee;\n  --color-primary-white: #ffffff;\n  --color-primary-light: #f9f9f9;\n  --color-primary-dark: rgb(33, 36, 41);\n  --color-secondary-dark: rgba(53, 51, 51, 0.7);\n  --color-brand-normal: #819b57;\n  --color-brand-dark: #465929;\n  --color-brand-yellow: #ff9f0e;\n  --color-primary-brown: #31261a;\n  --color-brand-light: #cfcfbc;\n  --color-secondary: #a18268;\n  --color-secondary-action: #970404;\n  --color-secondary-brand: #0B6473;\n  --color-secondary-text: #839aa9;\n  --gradient: linear-gradient(180deg, #ffb73b 0%, #ff2525 74%);\n  --error: red;\n}\n\n*,\n*::after,\n*::before {\n  box-sizing: border-box;\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\nbody {\n  font-family: \"Poppins\", sans-serif;\n  font-size: 14px;\n  font-weight: 300;\n  line-height: 1.5;\n  margin: 0;\n  color: var(--gray-darker);\n  background-color: var(--color-primary);\n  cursor: default;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np {\n  margin: 0;\n}\n\nimg {\n  display: block;\n  max-width: 100%;\n  height: auto;\n  object-fit: cover;\n}\n\nul {\n  list-style-type: none;\n  margin: 0;\n  padding: 0;\n}\n\na {\n  display: inline-block;\n}\n\naddress {\n  font-style: normal;\n}\n\nmain {\n  position: relative;\n}\n\n.alert {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.6);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;\n}\n.alert__box {\n  background-color: var(--color-primary);\n  color: var(--gray-darker);\n  padding: 24px;\n  border-radius: 10px;\n  text-align: center;\n  min-width: 300px;\n  box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2);\n  animation: fadeIn 0.3s ease-in-out;\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n}\n.alert__title {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 10px;\n}\n.alert__text {\n  font-size: 20px;\n}\n.alert__text--second {\n  font-size: 18px;\n}\n.alert__ok {\n  width: 10px;\n  height: 20px;\n  border-bottom: 3px solid var(--color-brand-normal);\n  border-right: 3px solid var(--color-brand-normal);\n  transform: rotate(-45deg) scale(0);\n  animation: scaleUp 0.4s ease-out forwards;\n}\n\n@keyframes fadeIn {\n  from {\n    transform: scale(0.8);\n    opacity: 0;\n  }\n  to {\n    transform: scale(1);\n    opacity: 1;\n  }\n}\n@keyframes scaleUp {\n  from {\n    transform: rotate(45deg) scale(0);\n  }\n  to {\n    transform: rotate(45deg) scale(1);\n  }\n}\n.button {\n  border-radius: 8px;\n  padding: 16px 32px;\n  cursor: pointer;\n  font-size: 16px;\n  font-weight: 600;\n  color: var(--color-primary-light);\n  font-family: \"Poppins\", sans-serif;\n  display: block;\n  background-color: var(--color-brand-normal);\n  border: 2px solid var(--color-brand-normal);\n  margin: 0 auto 16px;\n}\n.button__counter {\n  font-size: 26px;\n  box-shadow: 0px 1px 5px 3px rgba(0, 0, 0, 0.25);\n  animation: heartbeat 3s infinite;\n}\n.button:hover {\n  background: transparent;\n  color: var(--color-brand-dark);\n}\n\n@keyframes heartbeat {\n  0% {\n    transform: scale(1);\n  }\n  40% {\n    transform: scale(1.1);\n  }\n  80% {\n    transform: scale(1);\n  }\n  100% {\n    transform: scale(1);\n  }\n}\n.carousel-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-shrink: 0;\n  width: 34px;\n  height: 34px;\n  border: none;\n  background-color: transparent;\n  cursor: pointer;\n  fill: var(--color-primary-dark);\n}\n.carousel-button:hover {\n  fill: var(--color-brand-dark);\n}\n.carousel-button.prev {\n  transform: rotate(180deg);\n}\n\n.clock {\n  margin: 20px 0;\n  display: flex;\n  gap: 25px;\n  justify-content: center;\n  align-items: center;\n}\n.clock__container {\n  padding: 10px 15px;\n  background-image: radial-gradient(circle at 50% -20.71%, #c9f3de 53.33%, #c5f3e3 56.67%, #c2f3e8 60%, #bff3ec 63.33%, #bdf3f1 66.67%, #bcf3f6 70%, #bcf3fa 73.33%, #bdf2fe 76.67%, #bef1ff 80%, #c1f0ff 83.33%, #c5efff 86.67%, #c9eeff 90%, #cdedff 93.33%, #d3ebff 96.67%, #d8eaff 100%);\n  color: #424242;\n  border-radius: 3px;\n}\n.clock__number {\n  font-size: 40px;\n  font-weight: 700;\n  line-height: 1;\n  font-variant-numeric: tabular-nums;\n}\n\n.input {\n  position: relative;\n}\n.input__label {\n  position: absolute;\n  top: 16px;\n  left: 42px;\n}\n.input__label-order {\n  color: var(--color-secondary-dark);\n  margin: 0;\n}\n.input__control {\n  padding: 8px 24px;\n  width: 100%;\n  font-size: 16px;\n  line-height: 1.17;\n  border-radius: 8px;\n  border: 1px solid var(--color-secondary);\n  background-color: transparent;\n}\n.input__control:focus {\n  border-color: var(--color-brand-normal);\n  outline: none;\n}\n.input__control::placeholder {\n  color: var(--color-secondary);\n  font-size: 16px;\n  line-height: 1.17;\n}\n.input__control:focus::placeholder {\n  color: var(--color-primary-dark);\n}\n.input__radio {\n  display: none;\n}\n.input__radio:checked + label {\n  color: var(--color-brand-normal);\n}\n.input__radio:checked + label:before {\n  background: var(--color-brand-normal);\n  border-color: var(--color-brand-normal);\n}\n.input__radio--label {\n  width: 100%;\n  cursor: pointer;\n  position: relative;\n  margin-left: 35px;\n}\n.input__radio--label::before, .input__radio--label::after {\n  border-radius: 5px;\n  position: absolute;\n  top: -1.5px;\n  left: -35px;\n  transition: all 0.3s ease-out;\n  z-index: 2;\n}\n.input__radio--label::before {\n  content: \"\";\n  border: 1.5px solid #E4E4E4;\n  width: 20px;\n  height: 20px;\n}\n.input__radio--label::after {\n  content: \"\";\n  ba