\n}\n.pagination__button--active {\n  background-color: var(--color-brand-normal);\n}\n\n.review {\n  padding: 10px;\n  gap: 10px;\n  display: flex;\n  flex-direction: column;\n  border-radius: 8px;\n  border: 1.34px solid #e7eaec;\n  font-size: 18px;\n}\n.review__text {\n  display: flex;\n  justify-content: space-between;\n}\n.review__name {\n  font-size: 20px;\n  font-weight: 600;\n}\n.review__date {\n  font-size: 16px;\n  color: var(--color-secondary-text);\n}\n.review__icon {\n  fill: #EBB238;\n}\n\n.benefits {\n  display: flex;\n  justify-content: space-around;\n  padding: 12px 0 12px 18px;\n  background: linear-gradient(92deg, #00d4f7 13.54%, rgb(221, 174, 65) 85.29%);\n}\n.benefits__item {\n  display: flex;\n  font-size: 12px;\n  font-weight: 800;\n  align-items: center;\n  width: 30%;\n  gap: 8px;\n}\n.benefits__icon-container {\n  background: var(--color-primary-dark);\n  border-radius: 50%;\n  padding: 8px;\n  width: 34px;\n  height: 34px;\n  display: flex;\n  align-items: center;\n}\n.benefits__icon {\n  fill: var(--color-primary);\n}\n\n.container {\n  width: 100%;\n  max-width: 480px;\n  margin: 0 auto;\n  background-color: var(--color-primary-white);\n}\n\n.counter {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.counter__text {\n  margin: 20px 0 0;\n  font-size: 18px;\n}\n.counter__clock-container {\n  width: 100%;\n  text-align: center;\n}\n\n.description {\n  margin: 25px 0;\n}\n.description__title {\n  font-weight: 900;\n  font-size: 27px;\n  line-height: 32px;\n  text-align: center;\n  text-transform: uppercase;\n  margin: 0 0 30px;\n  color: #20222d;\n}\n.description__title::after {\n  display: block;\n  content: \"\";\n  width: 62px;\n  height: 5px;\n  margin: 15px auto;\n  background: url(\"../../public/images/separator.png\") center center no-repeat;\n}\n.description__text {\n  padding: 0 18px;\n  font-size: 16px;\n}\n.description__image {\n  margin: 12px 0;\n}\n\n.features {\n  margin: 25px 40px;\n}\n.features__title {\n  margin: 0 0 30px;\n  font-weight: 900;\n  font-size: 27px;\n  line-height: 32px;\n  text-align: center;\n  text-transform: uppercase;\n  color: #20222d;\n}\n.features__title::after {\n  display: block;\n  content: \"\";\n  width: 62px;\n  height: 5px;\n  margin: 15px auto;\n  background: url(\"../../public/images/separator.png\") center center no-repeat;\n}\n.features__list {\n  margin: 25px 0;\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n  font-size: 16px;\n  font-weight: 500;\n}\n.features__item {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n.features__item--align {\n  align-items: normal;\n}\n.features__icon {\n  fill: var(--color-brand-normal);\n}\n\n.footer {\n  padding: 32px 0;\n  background-color: var(--color-primary-dark);\n}\n.footer__container {\n  display: flex;\n  gap: 18px;\n  flex-direction: column;\n  cursor: default;\n}\n.footer__title {\n  margin-bottom: 14px;\n}\n.footer__address {\n  display: flex;\n  flex-direction: column;\n  gap: 24px;\n  text-align: center;\n}\n.footer__address-contacts {\n  color: var(--color-primary-light);\n}\n.footer__address-list, .footer__address-info {\n  flex-grow: 1;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n.footer__address-list--item {\n  display: flex;\n  gap: 8px;\n  justify-content: center;\n}\n.footer__address-link {\n  font-size: 16px;\n  font-weight: 300;\n  opacity: 0.8;\n}\n.footer__address-link--policy {\n  margin-top: 12px;\n}\n.footer__address-link:hover {\n  color: var(--color-brand-normal);\n}\n.footer__schedule {\n  font-size: 16px;\n  color: var(--color-primary-light);\n}\n.footer__address-copyright {\n  color: var(--color-primary-light);\n  font-size: 12px;\n  opacity: 0.6;\n  margin-top: 10px;\n}\n\n.guarantees__title {\n  margin: 0 0 30px;\n  font-weight: 900;\n  font-size: 27px;\n  line-height: 32px;\n  text-align: center;\n  text-transform: uppercase;\n}\n\n.header__title {\n  padding: 15px 20px 18px 20px;\n  text-align: center;\n  font-size